<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Scrollbar 容器</h2>
            <p slot="title" >用于对浏览器的滚动条样式进行处理的容器组件，方便快速搭建页面的基本结构：
<br/>
&lt;sh-scrollbar&gt;：外层容器。当子元素中包含所进行原始滚动的容器。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基本应用</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div class="dome1">
                            <sh-scrollbar>
                                <div class="dome2">
                                    test
                                </div>
                            </sh-scrollbar>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div class=&quot;dome1&quot;&gt;
    &lt;sh-scrollbar&gt;
        &lt;div class=&quot;dome2&quot;&gt;
            test
        &lt;/div&gt;
    &lt;/sh-scrollbar&gt;
&lt;/div&gt;

&lt;style&gt;
.dome1{
    width: 300px;   
    height: 300px;
    overflow: hidden;   //
    .dome2{
        width: 1600px;
        height: 1600px;
        background: #7a8690;    
    }
}
&lt;/style&gt;

                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Scrollbar',
  data(){
    return {
        isActive: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
.dome1{
    width: 300px;
    height: 300px;
    overflow: hidden;
    .dome2{
        width: 1600px;
        height: 1600px;
        background: #7a8690;
    }
}
</style>



