<template>
  <aside class="sh-aside" :style="{ width }">
    <slot></slot>
  </aside>
</template>

<script>
export default {
  name: 'ShAside',
  props: {
    width: {
      type: String,
      default: '300px'
    }
  }
};
</script>

<style lang="scss" scoped>
.sh-aside {
	overflow:auto;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	-ms-flex-negative:0;
	flex-shrink:0
}
</style>



