<template>
    <div class="sh-input" :class="{'sh-input--suffix': showSuffix}">
        <input
            class="sh-input__inner"
            :class="{'is-disabled': disabled}"
            :placeholder="placeholder"
            :type="showPassword ? (passwordVisible ? 'text':'password') : type"
            :name="name"
            :disabled="disabled"
            :value="value"
            @input="handleInput"
            @mouseover="fun1"
            @mouseleave="fun1"
            @focus="fun2"
            @blur="fun2"
        >
        <span class="sh-input__suffix" v-if="showSuffix ? (flge1 || flge2) : false">
            <i class="sh-input__icon sh-icon-circle-close" v-if="clearable && value" @click="clear"></i>
            <i class="sh-input__icon sh-icon-view" :class="{'sh-icon-view-active': passwordVisible}" v-if="showPassword" @click="handlePassword"></i>
        </span>
    </div>
</template>

<script>

export default {
  name: 'ShInput',
  data(){
      return {
          passwordVisible: false,
          flge1: false,
          flge2: false,
      }
  },
  props:{
      disabled:{
          type: Boolean,
          default: false
      },
      placeholder:{
          type: String,
          default: ''
      },
      type:{
          type: String,
          default: 'text'
      },
      name:{
          type: Boolean,
          default: false
      },
      value:{
          type: String,
          default: ''
      },
      clearable:{
          type: Boolean,
          default: false
      },
      showPassword:{
          type: Boolean,
          default: false
      }
  },
  computed:{
      showSuffix (){
          return this.clearable || this.showPassword
      }
  },
  methods: {
    handleInput(e){
      return this.$emit('input',e.target.value)
    },
    clear(){
        this.$emit('input','')
    },
    handlePassword(){
        this.passwordVisible = !this.passwordVisible
    },
    fun1(){
        this.flge1 = !this.flge1
    },
    fun2(){
        this.flge2 = !this.flge2
    }
  }
}
</script>

<style lang="scss" scoped>
.sh-input {
	position:relative;
	font-size:14px;
	display:inline-block;
	width:100%;
    .sh-input__inner {
        -webkit-appearance:none;
        background-color:#FFF;
        background-image:none;
        border-radius:4px;
        border:1px solid #DCDFE6;
        -webkit-box-sizing:border-box;
        box-sizing:border-box;
        color:#606266;
        display:inline-block;
        font-size:inherit;
        height:40px;
        line-height:40px;
        outline:0;
        padding:0 15px;
        -webkit-transition:border-color .2s cubic-bezier(.645,.045,.355,1);
        transition:border-color .2s cubic-bezier(.645,.045,.355,1);
        width:100%;
        &:focus {
            outline:0;
            border-color:#409EFF
        }
        &.is-disabled {
            background-color:#F5F7FA;
            border-color:#E4E7ED;
            color:#C0C4CC;
            cursor:not-allowed
        }
    }
}
.sh-input--suffix{
    .sh-input__inner {
        padding-right:30px
    }
    .sh-input__suffix{
        position:absolute;
        height: 100%;
        right: 10px;
        top: 0;
        line-height: 40px;
        text-align: center;
        transition: all .3s;
        -webkit-transition:all .3s;
        z-index: 900;
        i{
            color: #c0c4cc;
            font-size: 14px;
            cursor: pointer;
            transition: color .2s cubic-bezier(.645,.945,.355,1);
        }
        .sh-icon-view-active{
            color: red;
        }
    }
}
</style>



