<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Layout 布局</h2>
            <p slot="title" >通过基础的 24 分栏，迅速简便地创建布局。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础布局</h3>
            <p slot="title" >使用单一分栏创建基础的栅格布局。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-row>
                            <sh-col :span="8"><div class="color-1f2d3d">5</div></sh-col>
                            <sh-col :span="8"><div class="color-ddd">6</div></sh-col>
                            <sh-col :span="8"><div class="color-fff">7</div></sh-col>
                        </sh-row>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>通过 row 和 col 组件，并通过 col 组件的 span 属性我们就可以自由地组合布局。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;sh-row &gt;
    &lt;sh-col :span=&quot;8&quot;&gt;5&lt;/sh-col&gt;
    &lt;sh-col :span=&quot;8&quot;&gt;6&lt;/sh-col&gt;
    &lt;sh-col :span=&quot;8&quot;&gt;7&lt;/sh-col&gt;
&lt;/sh-row&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >分栏间隔</h3>
            <p slot="title" >分栏之间存在间隔。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-row :gutter="20">
                            <sh-col :span="8"><div class="color-1f2d3d">5</div></sh-col>
                            <sh-col :span="8"><div class="color-ddd">6</div></sh-col>
                            <sh-col :span="8"><div class="color-fff">7</div></sh-col>
                        </sh-row>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>Row 组件 提供 gutter 属性来指定每一栏之间的间隔，默认间隔为 0。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;el-row :gutter=&quot;20&quot;&gt;
  &lt;el-col :span=&quot;6&quot;&gt;1&lt;/el-col&gt;
  &lt;el-col :span=&quot;6&quot;&gt;2&lt;/el-col&gt;
  &lt;el-col :span=&quot;6&quot;&gt;3&lt;/el-col&gt;
  &lt;el-col :span=&quot;6&quot;&gt;4&lt;/el-col&gt;
&lt;/el-row&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Layout',
  data(){
    return {
        isActive: false,
        isActive1: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



