<template>
  <header class="sh-header" :style="{ height }">
    <slot></slot>
  </header>
</template>

<script>
export default {
  name: 'ShHeader',
  props: {
    height: {
      type: String,
      default: '60px'
    }
  }
};
</script>

<style lang="scss" scoped>
.sh-header {
	padding:0 20px;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	-ms-flex-negative:0;
	flex-shrink:0
}
</style>



