<template>
  <div class="sh-image">
    <img
      class="sh-image__inner"
      @click="clickHandler"
      :src="src"
      :class="imageClass"
      alt=""
      >
    <image-viewer :src="src" :z-index="zIndex" :visible.sync="visible"/>
  </div>
</template>

<script>
import ImageViewer from './imageviewer';
const ObjectFit = {
  NONE: 'none',//不做处理
  CONTAIN: 'contain',//保留尺寸的缩放
  COVER: 'cover',//保留尺寸缩放并裁剪
  FILL: 'fill',//拉伸
  SCALE_DOWN: 'scale-down'//保留尺寸缩放
};
export default {
    name: 'ShImage',
    components: {
      ImageViewer
    },
    props:{
      fit:{
        type: String,
        default: 'fill'
      },
      src:{
        type: String,
        required: true,
        default: ''
      },
      zIndex: {
        type: Number,
        default: 2000
      }
    },
    data() {
      return {
        imageWidth: 0,
        imageHeight: 0,
        visible: false
      };
    },
    computed: {
      imageClass() {
        for(let key  in ObjectFit){
          if(ObjectFit[key] == this.fit){
            return 'sh-'+ ObjectFit[key]
          }
        }
        return ''
      }
    },
    methods:{
      clickHandler(){
        this.visible = true
      }
    }
};

</script>

<style lang="scss" scoped>
.sh-image {
	position:relative;
	display:inline-block;
	overflow:hidden
}
.sh-image__inner {
	vertical-align:top;
  width: 100%;
  height: 100%;
}
.sh-fill {
  object-fit: fill;
}
.sh-contain {
  object-fit: contain;
}
.sh-cover {
  object-fit: cover;
}
.sh-none {
  object-fit: none;
}
.sh-scale-down {
  object-fit: scale-down;
}
</style>


