<template>
    <transition name="dialog-fade">
        <div class="sh-dialog__wrapper" v-show="visible" @click.self="handleClick">
            <div class="sh-dialog" :style="{marginTop: top,width}">
                <div class="sh-dialog__header">
                    <slot name="title">
                        <span class="sh-dialog__title">{{title}}</span>
                    </slot>
                    <button type="button" class="sh-dialog__headerbtn">
                        <i class="sh-icon-close"></i>
                    </button>
                </div>
                <div class="sh-dialog__body">
                    <!-- 默认插槽 -->
                    <slot></slot>
                </div>
                <div class="sh-dialog__footer" v-if="$slots.footer">
                    <slot name="footer"></slot>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>

export default {
  name: 'ShDialog',
  props:{
      title:{
          type: String,
          default: '提示'
      },
      width:{
          type: String,
          default: '50%'
      },
      top:{
          type: String,
          default: '15vh'
      },
      visible: {
          type: Boolean,
          default: false
      }
  },
  methods: {
    handleClick(){
      this.$emit('update:visible',false)
    }
  }
}
</script>

<style lang="scss" scoped>
.sh-dialog {
	position:relative;
	margin:0 auto 50px;
	background:#FFF;
	border-radius:2px;
	-webkit-box-shadow:0 1px 3px rgba(0,0,0,.3);
	box-shadow:0 1px 3px rgba(0,0,0,.3);
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	width:50%
}
.sh-dialog__wrapper {
	position:fixed;
	top:0;
	right:0;
	bottom:0;
	left:0;
	overflow:auto;
	margin:0
}
.sh-dialog__header {
	padding:20px 20px 10px
}
.sh-dialog__headerbtn {
	position:absolute;
	top:20px;
	right:20px;
	padding:0;
	background:0 0;
	border:none;
	outline:0;
	cursor:pointer;
	font-size:16px
}
.sh-dialog__headerbtn .sh-dialog__close {
	color:#909399
}
.sh-dialog__headerbtn:focus .sh-dialog__close,.sh-dialog__headerbtn:hover .sh-dialog__close {
	color:#409EFF
}
.sh-dialog__title {
	line-height:24px;
	font-size:18px;
	color:#303133
}
.sh-dialog__body {
	padding:30px 20px;
	color:#606266;
	font-size:14px;
	word-break:break-all
}
.sh-dialog__footer {
	padding:10px 20px 20px;
	text-align:right;
	-webkit-box-sizing:border-box;
	box-sizing:border-box
}
.sh-dialog--center {
	text-align:center
}
.sh-dialog--center .sh-dialog__body {
	text-align:initial;
	padding:25px 25px 30px
}
.sh-dialog--center .sh-dialog__footer {
	text-align:inherit
}
.dialog-fade-enter-active {
	-webkit-animation:dialog-fade-in .3s;
	animation:dialog-fade-in .3s
}
.dialog-fade-leave-active {
	-webkit-animation:dialog-fade-out .3s;
	animation:dialog-fade-out .3s
}
@-webkit-keyframes dialog-fade-in {
	0% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
    100% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:1
    }
}
@keyframes dialog-fade-in {
	0% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
    100% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:1
    }
}
@-webkit-keyframes dialog-fade-out {
	0% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:1
    }
    100% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
}
@keyframes dialog-fade-out {
	0% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:1
    }
    100% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
}
</style>



