<template>
    <div class="sh-scrollbar" @mouseenter="enters" @mouseleave="leaver">
            <span v-shdragY v-if="flagy" v-show="hovery" class="y-but" ref="sy" ></span>
        <div class="sh-scrollbar-overflow" ref="scrollDom">
            <slot></slot>
        </div>
            <span v-shdragX v-if="flagx" v-show="hoverx" class="x-but" ref="sx"></span>
    </div>
</template>

<script>

export default {
  name: 'ShScrollbar',
  data(){
      return {
          flagy: true, //是否需要Y轴滚动条
          hovery: false, //是否显示Y轴滚动条
          flagx: true, //是否需要X轴滚动条
          hoverx: false, //是否显示x轴滚动条
          timeout: null, //DOM发生变化后滚动条重置
      }
  },
  props:{
  },
  methods: {
    rollingheight:function(e){
        e.preventDefault();
        this.hovery = true
        console.log("rollingheight")
        //console.log(this.$refs.scrollDom.scrollHeight)  //总高度
        //console.log(this.$refs.scrollDom.scrollTop)   //当前滚动的高度
        //console.log(this.$refs.scrollDom.clientHeight) //显示的高度
        if(this.$refs.sy){
            this.$refs.sy.style.top = this.$refs.scrollDom.clientHeight * (this.$refs.scrollDom.scrollTop / this.$refs.scrollDom.scrollHeight) + "px";
        }
    },
    enters(){
        this.hovery = true;
        this.hoverx = true;
    },
    leaver(){
        this.hovery = false;
        this.hoverx = false;
    },
    fun(){
        if(this.$refs.scrollDom){
        this.flagx = (this.$refs.scrollDom.scrollWidth - this.$refs.scrollDom.clientWidth) > 0 ? true:false
        if(this.flagx){
            //设置滚动条宽度
            var _w = this.$refs.scrollDom.clientWidth * (this.$refs.scrollDom.clientWidth / this.$refs.scrollDom.scrollWidth) 
            this.$nextTick(() => {
                this.$refs.sx.style.Width = _w + "px";
            })
        }
        this.flagy = (this.$refs.scrollDom.scrollHeight - this.$refs.scrollDom.clientHeight) > 0 ? true:false
        if(this.flagy){
            //设置滚动条高度
            var _h = this.$refs.scrollDom.clientHeight * (this.$refs.scrollDom.clientHeight / this.$refs.scrollDom.scrollHeight) 
            this.$nextTick(() => {
                this.$refs.sy.style.height = _h + "px";
            })
            this.$refs.scrollDom.addEventListener('scroll',this.rollingheight,true)
        }
        }
    }
  },
  mounted() {
    this.$nextTick(() => {
        this.fun()
        new ResizeObserver(entries => {
            for (let entry of entries) {
                console.log("宽高变化");
                //console.log(entry.target.scrollHeight,this.$refs.scrollDom.scrollHeight)
                clearTimeout(this.timeout);
                this.timeout = setTimeout(()=> {
                    this.$nextTick(() => {
                        this.fun()
                    })
                }, 300);
            }
        }).observe(this.$refs.scrollDom.firstChild);
    })
  },
    directives: {
        shdragX: {
            // 指令的定义
            inserted: (el,binding,vnode) =>{
                let that = vnode.context  
                el.onmousedown = function(e) {
                    //计算出元素距离上边和左边的距离（鼠标点击的位置-元素的位置）
                    var disX = e.clientX - el.offsetLeft;
                    document.onmousemove = function(e) {
                        that.hoverx = true;
                        //鼠标要按住不松开移动才行，松开就会触发onmouseup的事件
                        //计算出元素移动后的位置（鼠标位置-元素初始的disX/disY）
                        var l = e.clientX - disX;
                        if( 0 > l ){l = 0}
                        if( (that.$refs.scrollDom.clientWidth - el.offsetWidth) < l ){l = that.$refs.scrollDom.clientWidth - el.offsetWidth}
                        that.$refs.scrollDom.scrollLeft = that.$refs.scrollDom.scrollWidth * (l / that.$refs.scrollDom.clientWidth)
                        el.style.left = l + 'px';
                        e.preventDefault();
                    };
                    document.onmouseup = function(e) {
                        document.onmousemove = null;
                        document.onmouseup = null;
                    };
                };
            }
        },
        shdragY: {
            // 指令的定义
            inserted: (el,binding,vnode) =>{
                let that = vnode.context  
                el.onmousedown = function(e) {
                    var disY = e.clientY - el.offsetTop;
                    document.onmousemove = function(e) {
                        var t = e.clientY - disY;
                        if( 0 > t ){t = 0}
                        if( (that.$refs.scrollDom.clientHeight - el.offsetHeight) < t ){t = that.$refs.scrollDom.clientHeight - el.offsetHeight}
                        el.style.top = t + 'px';
                        that.$refs.scrollDom.scrollTop = that.$refs.scrollDom.scrollHeight * (t / that.$refs.scrollDom.clientHeight)
                        e.preventDefault();
                    };
                    document.onmouseup = function(e) {
                        document.onmousemove = null;
                        document.onmouseup = null;
                    };
                };
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.sh-scrollbar{
    height: 100%;
    width: 100%;
    overflow: hidden;
    position: relative;
    >.sh-scrollbar-overflow{
        overflow: scroll;
        height: calc(100% + 17px);
        width: calc(100% + 17px);
        position: absolute;
    }
    >.y-but{
        position: absolute;
        top: 0px;
        right: 0px;
        height: 100px;
        width:8px;
        display:block;
        background: #ddd;
        border-radius: 5px;
        z-index: 2021;
    }
    >.x-but{
        position: absolute;
        left: 0px;
        bottom: 0px;
        height: 8px;
        width:100px;
        display:block;
        background: #ddd;
        border-radius: 5px;
        z-index: 2021;
    }
}
</style>



