import '../css/col.scss'
export default {
    name: 'ShCol',
    props: {
        span: {
        type: Number,
        default: 24
        },
        tag: {
        type: String,
        default: 'div'
        },
        offset: Number,
        pull: Number,
        push: Number,
        xs: [Number, Object], // 响应式栅格数或者栅格属性对象
        sm: [Number, Object], // (例如： {span: 4, offset: 4})	 同下
        md: [Number, Object],
        lg: [Number, Object],
        xl: [Number, Object]
    },

    computed: {
        gutter() {
        let parent = this.$parent;
        while (parent && parent.$options.componentName !== 'ShRow') {
            /*parent.$options.componentName 也就是 row 中的 componentName 
                迭代：直到找到父元素是 Row 并且获取 Row 的 gutter 属性
            */
            parent = parent.$parent;
        }
        return parent ? parent.gutter : 0;
        }
    },
    render(h) {
        let classList = [];
        let style = {};

        if (this.gutter) { //由计算属性而来，也是通过 row 的 gutter 的值而来 
        style.paddingLeft = this.gutter / 2 + 'px';
        style.paddingRight = style.paddingLeft;
            /*采用padding达到间隔效果*/
        }

        ['span', 'offset', 'pull', 'push'].forEach(prop => {
        if (this[prop] || this[prop] === 0) {
            classList.push(
            prop !== 'span'
                ? `sh-col-${prop}-${this[prop]}`
                : `sh-col-${this[prop]}`
            );
        }
        });

        ['xs', 'sm', 'md', 'lg', 'xl'].forEach(size => {
        if (typeof this[size] === 'number') {
            classList.push(`sh-col-${size}-${this[size]}`);
        } else if (typeof this[size] === 'object') {
            let props = this[size];
            Object.keys(props).forEach(prop => {
            classList.push(
                prop !== 'span'
                ? `sh-col-${size}-${prop}-${props[prop]}`
                : `sh-col-${size}-${props[prop]}`
            );
            });
        }
        });

        return h(this.tag, {
        class: ['sh-col', classList],
        style
        }, this.$slots.default);
    }
};
  
  


