import '../css/row.scss'
export default {
  name: 'ShRow', //组件名字，作用在 button 组件中，已经说明了
  componentName: 'ShRow', //这个在 col 组件中判断 是否为 col 的父组件，从而给 gutter 间隔
  props: {
    tag: { //自定义元素标签, tag 在 render 函数中定义元素标签，
        //题外话 在vue中使用 is 也是可以改变元素标签的 ps: <p is="span"></p> 在控制台是span标签
        //场景：C端 SEO,要提供语义化的 HTML 标签，这个时候可以使用 tag
      type: String,
      default: 'div'
    },
    gutter: Number, //间隔大小
    type: String, // 布局模式，可选 flex，
    justify: { // flex 布局下的水平排列方式
      type: String,
      default: 'start'
    },
    align: { //flex 布局下的垂直排列方式
      type: String,
      default: 'top'
    }
  },

  computed: {
    style() {
      const ret = {};
      if (this.gutter) {
        ret.marginLeft = `-${this.gutter / 2}px`;
        ret.marginRight = ret.marginLeft;
      }
      return ret;
    }
  },

  render(h) {  //render 函数
    return h(this.tag, {
      class: [
        'sh-row',
        this.justify !== 'start' ? `is-justify-${this.justify}` : '',
        this.align !== 'top' ? `is-align-${this.align}` : '',
        { 'sh-row--flex': this.type === 'flex' } //是否为 flex 布局
      ],
      style: this.style //object 样式 由计算属性得出
    }, this.$slots.default); //插入默认插槽
  }
};


