<template>
    <div class="sh-nav">
        <div class="sh-nav-title">
            <div class="sh-nav-conten" ref="shNavDomViwe">
                <div class="sh-nav-content-list" ref="shNavDom">
                    <div class="tabBarBtn" :class="[tabName=='Tab1'?'tabBarBtnActive':'']" 
                    @click="tabName='Tab1'">Tab1</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab2'?'tabBarBtnActive':'']"
                        @click="tabName='Tab2'">Tab2</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab3'?'tabBarBtnActive':'']"
                        @click="tabName='Tab3'">Tab3</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab4'?'tabBarBtnActive':'']"
                        @click="tabName='Tab4'">Tab4</div>
                </div> 
            </div>
        </div>
        <div>
            {{ tabName }}
        </div>
    </div>
</template>

<script>
export default {
    name: 'test',
    data() {
        return {
            tabName: 'Tab1',
            flag: false,
        };
    },
    methods:{
    },
    mounted(){
        //this.fun3()
    }
};
</script>
<style lang="scss" scoped>
.sh-nav{
    height: 30px;
    .sh-nav-title{
        font-size: 0;
        color: #303133;
        >span:hover{
            color: 409eff;
        }
        .sh-nav-conten{
            font-size: 14px;
            line-height: 30px;
            text-align: center;
            width: calc(100% - 42px);
            display: inline-block;
            overflow: hidden;
            vertical-align: top;
            .sh-nav-content-list{
                position: relative;
                height: 30px;
                white-space: nowrap;
                display: inline-flex;
                min-width: calc(100% - 42px);
                border-bottom: 3px solid #ddd;
                .tabBarBtn{
                    float:left;
                    height: calc(30px - 3px);
                    box-sizing: border-box;
                    display: inline-block;
                    margin: auto 13px;
                    min-width: 100px;
                    cursor: pointer;
                }
                >.tabBarBtn:hover{
                    color: #409eff;
                }
                .tabBarBtnActive{
                    border-bottom: 3px solid #409eff;
                    color: #409eff;
                }
            }
            .sh-nav-content-list:after{
                content: " ";
                display: block;
                height: 0;
                font-size: 0;
            }
        }
    }
}
</style>


