import ShButton from './button.vue'
import ShDialog from './dialog.vue'
import ShInput from './input.vue'
import ShSwitch from './switch.vue'
import ShContainer from './container.vue'
import ShMain from './main.vue'
import ShHeader from './header.vue'
import ShFooter from './footer.vue'
import ShAside from './aside.vue'
import ShImage from './image.vue'
import ShTabs from './tabs/tabs.vue'
import ShTabSpan from './tabs/tabpaen.vue'
import test from './tabs/tab.vue'
import ShScrollbar from './scrollbar.vue'
//import ShRow from './row.vue'
//import ShCol from './col.vue'

import ShRow from './js/row'
import ShCol from './js/col'

import './fonts/fonts.scss'

//
const components = [
    ShButton,
    ShDialog,
    ShInput,
    ShSwitch,
    ShContainer,
    ShMain,
    ShHeader,
    ShFooter,
    ShAside,
    ShRow,
    ShCol,
    ShImage,
    ShTabs,
    ShTabSpan,
    ShScrollbar,
    test
]

const install = function(Vue){
    components.forEach(itme => {
        Vue.component(itme.name,itme)
    });
}
//
if(typeof window !== 'undefined' && window.Vue){
    install(window.Vue)
}
export default {
    install
}


