<template>
    <transition name="showhide-image">
        <div class="sh-image-viewer" v-show="visible" @click.self="handleClick">
        <span class="sh-image-colse"  @click="handleClick"><i class="sh-icon-close"></i></span>
            <div class="sh-image-viewer-model">
                <div class="sh-image-viewer-bottom">
                    <div class="sh-image-viewer__actions__inner">
                        <i class="sh-icon-zoom-out" @click="handleActions('zoomOut')"></i>
                        <i class="sh-icon-zoom-in"  @click="handleActions('zoomIn')"></i>
                        <i v-show="flag2" class="sh-icon-c-scale-to-original" @click="handleActions('original')"></i>
                        <i v-show="!flag2" class="sh-icon-full-screen" @click="handleActions('original')"></i>
                        <i class="sh-icon-refresh-left" @click="handleActions('anticlocelise')"></i>
                        <i class="sh-icon-refresh-right" @click="handleActions('clocelise')"></i>
                    </div>
                </div>
            </div>
            <img
            v-drag
            class="sh-image__inner"
            :style="imageStyle"
            :src="src"
            alt="">
        </div>
    </transition>
</template>

<script>
export default {
    name: 'ShImageViewer',
    props:{
      src:{
        type: String,
        required: true,
        default: ''
      },
      zIndex: {
        type: Number,
        default: 2000
      },
      visible: {
          type: Boolean,
          default: false
      }
    },
    data() {
      return {
        currentId:1,
        listName: [], //滚动的列表
        imageStyle: '',
        t1: 1,
        t2: 360,
        lastX: null,
        lastY: null,
        flag1: false,
        flag2: false,
      };
    },
    computed: {
    },
    methods:{
        handleClick(){
            //
            this.t1 = 0.955
            this.imageStyle = "transform: scale("+this.t1+") rotate(-360deg); margin-left: 0px; margin-top: 0px;"
            //
            this.$emit('update:visible',false)
        },
        //判断滚动方向
        handleScroll(e){
            let direction = e.deltaY > 0 ? 'down' : 'up'
            //业务操作
            this.changeSel(direction); 
        },
        changeSel(val){
            switch(val) {
                case 'up':
                    this.t1 += 0.015
                    break;
                case 'down':
                    this.t1 -= 0.015
                    break;
                default:
                    break;
            } 
            this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg); "
        },
        handleActions(val){
            switch(val) {
                case 'zoomOut':
                    this.t1 -= 0.03
                    this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg);"
                    break;
                case 'zoomIn':
                    this.t1 += 0.03
                    this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg);"
                    break;
                case 'anticlocelise':
                    this.t2 += 90
                    this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg);"
                    break;
                case 'clocelise':
                    this.t2 -= 90
                    this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg);"
                    break;
                case 'original':
                    this.flag2 = !this.flag2
                    if(this.flag2){
                        this.t1 = 0.955 
                    }else{
                        this.t1 = 1
                    }
                    this.imageStyle = "transform: scale("+this.t1+") rotate(-"+this.t2+"deg); left:25%; top:0;"
                    break;
                default:
                    break;
            } 
        }
    },
    mounted () {
        //监听鼠标滚动事件 
        document.addEventListener('mousewheel', this.handleScroll, true)||document.addEventListener("DOMMouseScroll",this.handleScroll, false)
    }
}

</script>

<style lang="scss" scoped>

.sh-image-viewer {
	position:fixed;
	top:0;
	right:0;
	bottom:0;
	left:0;
	overflow:auto;
	margin:0;
    background: #808080;
    z-index: 2020;
    opacity: .8;
    .sh-image-colse{
        position: absolute;
        right: 100px;
        top: 50px;
        border: 2px solid #fff;
        width: 30px;
        height: 30px;
        border-radius: 17px;
        text-align: center;
        i{
            line-height: 30px;
            color: #fff;
        }
    }
    .sh-image__inner{
        position: fixed;
        height: 100%;
        margin:auto;
        left:25%;
        top:0;
        max-height: 100%; 
        max-width: 100%;
    }
    .sh-image-viewer-model {
        position:relative;
        margin:0 auto;
        border-radius:2px;
        -webkit-box-sizing:border-box;
        box-sizing:border-box;
        width: 350px;
        height: 100%;
        overflow: hidden;
        .sh-image-viewer-bottom{
            position: absolute;
            bottom: 30px;
            width: 304px;
            height: 44px;
            padding: 0 23px;
            background-color: #606266;
            border-color: #fff;
            border-radius: 22px;
            z-index: 1;
            .sh-image-viewer__actions__inner{
                width: 100%;
                height: 100%;
                text-align: justify;
                cursor: default;
                font-size: 23px;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: space-around;
                i{
                }
            }
        }
    }
}
.showhide-image-enter-active {
	-webkit-animation:showhide-image-in .3s;
	animation:showhide-image-in .3s
}
.showhide-image-leave-active {
	-webkit-animation:showhide-image-out .3s;
	animation:showhide-image-out .3s
}
@-webkit-keyframes showhide-image-in {
	0% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
    100% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:.7
    }
}
@keyframes showhide-image-in {
	0% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
    100% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:.7
    }
}
@-webkit-keyframes showhide-image-out {
	0% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:.7
    }
    100% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
}
@keyframes showhide-image-out {
	0% {
        -webkit-transform:translate3d(0,0,0);
        transform:translate3d(0,0,0);
        opacity:.7
    }
    100% {
        -webkit-transform:translate3d(0,-20px,0);
        transform:translate3d(0,-20px,0);
        opacity:0
    }
}
</style>


