      'is-plain': plain,
      'is-round': round,
      'is-circle': circle,
      'is-disabled': disabled
  }]"
  :disabled='disabled'
  @click="handleClick"
  >
    <i v-if="icon" :class="icon"></i>
    <span v-if="$slots.default"><slot></slot></span>
  </button>
</template>

<script>

export default {
  name: 'ShButton',
  props:{
      type:{
          type: String,
          default: 'default'
      },
      plain:{
          type: Boolean,
          default: false
      },
      round:{
          type: Boolean,
          default: false
      },
      circle:{
          type: Boolean,
          default: false
      },
      icon:{
          type: String,
          default: ''
      },
      disabled:{
          type: Boolean,
          default: false
      }
  },
  methods: {
      handleClick (e){
          this.$emit('click', e)
      }
  }
}
</script>

