<template>
    <div class="sh-switch" :class="{'is-checked':value}" @click="handleClick">
        <input
            class="sh-switch__input"
            type="checkbox"
            :name="name"
            ref="input"
        >
        <span class="sh-switch__core" ref="core">
            <span class="sh-switch__button"></span>
        </span>
    </div>
</template>

<script>

export default {
  name: 'ShSwitch',
  props:{
      value:{
          type: Boolean,
          default: false
      },
      activeColor:{
          type: String,
          default: ''
      },
      inactiveColor:{
          type: String,
          default:''
      },
      name:{
          type: String,
          default:''
      }
  },
  methods: {
    handleClick(){
        this.$emit('input',!this.value)
        this.$nextTick().then(()=>{
            this.setColor()
            this.$refs.input.checked = this.value
        })
    },
    setColor(){
        if(this.activeColor || this.inactiveColor){
            let color = this.value ? this.activeColor : this.inactiveColor
            this.$refs.core.style.borderColor = color
            this.$refs.core.style.backgroundColor = color
        }
    }
  },
  mounted(){
      this.setColor()
      this.$refs.input.checked = this.value
  }
}
</script>

<style lang="scss" scoped>
.sh-switch {
	display:-webkit-inline-box;
	display:-ms-inline-flexbox;
	display:inline-flex;
	-webkit-box-align:center;
	-ms-flex-align:center;
	align-items:center;
	position:relative;
	font-size:14px;
	line-height:20px;
	height:20px;
	vertical-align:middle;
    .sh-switch__input{
        position:absolute;
        width:0;
        height:0;
        opacity:0;
        margin:0;
    }
    .sh-switch__core{
        margin:0;
        display:inline-block;
        position:relative;
        width:40px;
        height:20px;
        border:1px solid #DCDFE6;
        outline:0;
        border-radius:10px;
        -webkit-box-sizing:border-box;
        box-sizing:border-box;
        background:#DCDFE6;
        cursor:pointer;
        -webkit-transition:border-color .3s,background-color .3s;
        transition:border-color .3s,background-color .3s;
        vertical-align:middle;
        .sh-switch__button{
            position:absolute;
            top:1px;
            left:1px;
            border-radius:100%;
            -webkit-transition:all .3s;
            transition:all .3s;
            width:16px;
            height:16px;
            background-color:#FFF;
        }
    }
}
.sh-switch.is-checked{
    .sh-switch__core{
        border-color:#409eff;
        background-color:#409eff;
        .sh-switch__button{
            transform:translateX(20px);
        }
    }
}
</style>



