<template>
    <sh-row>
        <sh-col :span="3">
            <ShImage 
            :src="src" 
            fit="fill"
            style="width: 40px; height: 40px"
            ></ShImage>
            <h2>SHUI</h2>
        </sh-col>
        <sh-col :span="21">
            <ul class="nav">
                <li class="nav-item">
                    <div class="nav-item-input">
                        <Sh-Input v-model="input"></Sh-Input>
                    </div>
                </li>
                <li class="nav-item" :class="[tabName=='Tab1'?'nav-item-active':'']" 
                    @click="tabName='Tab1'">指南</li>
                <li class="nav-item" :class="[tabName=='Tab2'?'nav-item-active':'']" 
                    @click="tabName='Tab2'">组件</li>
                <li class="nav-item" :class="[tabName=='Tab3'?'nav-item-active':'']" 
                    @click="tabName='Tab3'">主题</li>
                <li class="nav-item" :class="[tabName=='Tab4'?'nav-item-active':'']" 
                    @click="tabName='Tab4'">资源</li>
                <li class="nav-item nav-item-fenge">|</li>
                <li class="nav-item">
                    <div @click="flag1 = !flag1;flag2= false;">1.1.0<i class="sh-icon-arrow-down"></i></div>
                    <div v-show="flag1" class="sh-dropdown-menu">
                        <ul>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                            <li>1.1.0</li>
                        </ul>
                    </div>
                </li>
                <li class="nav-item">
                    <div @click="flag2 = !flag2;flag1= false;">CH<i class="sh-icon-arrow-down"></i></div>
                    <div v-show="flag2" class="sh-dropdown-menu">
                        <ul>
                            <li>ZN</li>
                            <li>EH</li>
                        </ul>
                    </div>
                </li>
            </ul>
        </sh-col>
    </sh-row>
</template>

<script>

export default {
  name: 'Navbar',
  data(){
    return {
      //src: require('../../packages/img/shui.png'),
      src: './static/img/shui.png',
      input: '',
      tabName: 'Tab2',
      flag1: false,
      flag2: false,
    }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
.sh-row{
    .sh-col{
        margin: 8px auto;
    }
    .sh-col-3{
        h2{
        display: inline-block;
        margin: 0;
        line-height: 40px;
        vertical-align: top;
        color: #d7251bc2;
        }
    }
    .sh-col-21{
        .nav{
            margin: 0;
            line-height: 49px;
            color: #ddd;
            text-align: right;
            .nav-item{
                text-align: center;
                min-width: 50px;
                display: inline-block;
                margin: 0 13px;
                .nav-item-input{
                    width: 200px;
                }
                .sh-dropdown-menu{
                    position: absolute;
                    ul{
                        list-style: none;
                        padding: 0;
                        li{
                            margin: 0;
                            padding: 0;
                            height: 30px;
                        }
                    }
                }
            }
            .nav-item:hover{
                color: #409eff;
                cursor: pointer;
                .sh-dropdown-menu{
                    color: #ddd;
                }
            }
            .nav-item-fenge{
                min-width: 5px;
                width: 5px !important;
                margin: 0;
            }
            .nav-item-fenge:hover{
                color: #ddd !important;
            }
            .nav-item-active{
                color: #409eff;
                border-bottom: 3px solid #409eff;
            }
        }
    }
}

</style>



