<template>
    <div class="sh-nav">
        <div class="sh-nav-title">
            <span v-if="flag" class="sh-nav-left" @click="fun1"><i class="sh-icon-arrow-left"></i></span>
            <div class="sh-nav-conten" ref="shNavDomViwe">
                <div class="sh-nav-content-list" ref="shNavDom">
                    <div class="tabBarBtn" :class="[tabName=='Tab1'?'tabBarBtnActive':'']" 
                    @click="tabName='Tab1'">Tab1</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab2'?'tabBarBtnActive':'']"
                        @click="tabName='Tab2'">Tab2</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab3'?'tabBarBtnActive':'']"
                        @click="tabName='Tab3'">Tab3</div>
                    <div class="tabBarBtn" :class="[tabName=='Tab4'?'tabBarBtnActive':'']"
                        @click="tabName='Tab4'">Tab4</div>
                </div> 
            </div>
            <span v-if="flag" class="sh-nav-right" @click="fun2"><i class="sh-icon-arrow-right"></i></span>
        </div>
        <div>
        
        </div>
    </div>
</template>

<script>
export default {
    name: 'ShTabs',
    data() {
        return {
            tabName: 'Tab1',
            navcount: 0,
            flag: false,
        };
    },
    methods:{
        fun1(){
            this.navcount += 5
            if(this.navcount >= 0){
                this.navcount = 0
            }
            this.$refs.shNavDom.style.left = this.navcount + 'px'
        },
        fun2(){
            this.navcount -= 5
            //获取显示长度
            const t1 = this.$refs.shNavDomViwe.clientWidth;
            //获取实际长度
            const t2 = this.$refs.shNavDom.clientWidth;
            //差值 nce
            if(this.navcount <= (t1 - t2)){
                this.navcount = (t1 - t2)
            }
            this.$refs.shNavDom.style.left = this.navcount + 'px'
        },
        fun3(){
            if ((this.$refs.shNavDomViwe.clientWidth - this.$refs.shNavDom.clientWidth) >= 0) {
                this.flag = false
            }else{
                this.flag = true
            }
        }
    },
    mounted(){
        this.fun3()
    }
};
</script>
<style lang="scss" scoped>
.sh-nav{
    height: 30px;
    .sh-nav-title{
        font-size: 0;
        color: #303133;
        >span:hover{
            color: 409eff;
        }
        .sh-nav-right,.sh-nav-left{
            font-size: 14px;
            line-height: 30px;
            height: 100%;
            display: inline-block;
            border-bottom: 3px solid #ddd;
            cursor: auto;
        }
        .sh-nav-conten{
            font-size: 14px;
            line-height: 30px;
            text-align: center;
            width: calc(100% - 42px);
            display: inline-block;
            overflow: hidden;
            vertical-align: top;
            .sh-nav-content-list{
                position: relative;
                height: 30px;
                white-space: nowrap;
                display: inline-flex;
                min-width: calc(100% - 42px);
                border-bottom: 3px solid #ddd;
                .tabBarBtn{
                    float:left;
                    height: calc(30px - 3px);
                    box-sizing: border-box;
                    display: inline-block;
                    margin: auto 13px;
                    min-width: 100px;
                    cursor: pointer;
                }
                >.tabBarBtn:hover{
                    color: #409eff;
                }
                .tabBarBtnActive{
                    border-bottom: 3px solid #409eff;
                    color: #409eff;
                }
            }
            .sh-nav-content-list:after{
                content: " ";
                display: block;
                height: 0;
                font-size: 0;
            }
        }
    }
}
</style>


